import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { FormBuilder } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { from, Observable, Subscriber } from 'rxjs';
import { AccessProviders } from '../../providers/access-providers';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';



//registro semana
import { semana_L } from '../modelo_L/modelo_L';

//calendario
import { CalendarComponentOptions } from 'ion2-calendar'




@Component({
  selector: 'app-menu-llanos',
  templateUrl: './menu-llanos.page.html',
  styleUrls: ['./menu-llanos.page.scss'],
})
export class MenuLlanosPage implements OnInit {

   //calendario
   dateMulti: string[];
   type: 'string';
 
   optionsMulti: CalendarComponentOptions = {
     pickMode: 'multi'
   };
 
    //del registro de la semana
 
    objeto:semana_L = { numsemana:"", descripcion:"", lunes:"", martes:"", miercoles:"", jueves:"", viernes:"", sabado:"", domingo:"" }
   
    semanas: semana_L[] = [];
    url2 = 'http://192.168.4.153/serve_api/menu_semanal_L.php';
    url = 'http://192.168.4.153/serve_api/crear_semana_L.php';
   

  constructor( 
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders,
    private actRoute: ActivatedRoute
     ) {
    this.listarSemanas();
   }

   getSemanas(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarSemanas(){
    this.semanas = [];
    this.getSemanas().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.semanas.push(res[i]);
      }
    })
  }

  agregarSemana(obj :semana_L): Observable<semana_L>{
    return this.http.post<semana_L>(this.url, JSON.stringify(obj))
  }

  async agregar() {
    this.agregarSemana(this.objeto)
    .subscribe((resp) =>{
      this.objeto.numsemana="";
      this.objeto.descripcion='';
      this.objeto.lunes='';
      this.objeto.martes='';
      this.objeto.miercoles='';
      this.objeto.jueves='';
      this.objeto.viernes='';
      this.objeto.sabado='';
      this.objeto.domingo='';
      this.listarSemanas();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }

  ngOnInit() {
  }
  
   //calendario
   onChange($event) {
    console.log($event);
  }

  //eliminar

  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_MSL',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarSemanas();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }


  abrirCrud(a){
    this.router.navigate(['/mod-menul/'+a]);
  }



}
